.order {
  @include image-set("../img/percent");
  padding: 87px 0;
  background-color: var(--accent-color);
  background-position: bottom center;
  background-size: cover;

  &__container {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
  }

  &__title {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 600;
    font-size: 36px;
    line-height: 47px;
    color: var(--light-color);
  }

  &__descr {
    margin: 0;
    font-weight: 400;
    font-size: 20px;
    line-height: 26px;
    color: var(--light-color);
  }

  &__form {
    max-width: 500px;
  }
}
.left {
  grid-column: 5 span;
  padding-left: 60px;

  @include high-tablet {
    margin-bottom: 30px;
    grid-column: 12 span;
    padding-left: 0;
  }
}
.right {
  display: flex;
  justify-content: flex-end;
  grid-column: 7 span;
  padding-right: 60px;

  @include high-tablet {
    justify-content: flex-start;
    grid-column: 12 span;
    padding-right: 0;
  }
}
