.popup {
  padding: 40px;
  width: 100%;
  max-width: 940px;
  &__top {
    display: flex;
    margin-bottom: 40px;
  }

  > graph-modal {
    &::before {
      @include tablet {
        display: none;
      }
    }
  }

  &__img {
    margin-right: 40px;
  }

  &__name {
    margin-bottom: 10px;
    font-weight: 600;
    font-size: 24px;
    line-height: 38px;
    color: var(--dark-color);
  }

  &__post {
    margin-bottom: 10px;
    font-weight: 400;
    font-size: 18px;
    line-height: 29px;
    color: var(--dark-color07);
  }

  &__bottom-content {
    padding-top: 20px;
    max-height: 375px;
  }

  &__descr {
    margin-bottom: 40px;
  }

  &__text {
    margin: 0;
    margin-bottom: 5px;
    font-weight: 400;
    font-size: 20px;
    line-height: 32px;
    color: var(--dark-color);
  }
  &__subtitle {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 600;
    font-size: 24px;
    line-height: 38px;
    color: var(--dark-color);
  }
  .simplebar-track.simplebar-vertical {
    border-radius: 40px;
    margin-top: 20px;
    margin-bottom: 20px;
    width: 5px;
    background: rgba(20, 16, 36, 0.08);
  }
  .simplebar-scrollbar::before {
    left: 0;
    right: 0;
    border-radius: 40px;
    opacity: 1;
    width: 5px;
    background: var(--dark-color);
  }
}

.tabs {
  &__menu {
    border-bottom: 1px solid rgba(20, 16, 36, 0.11);
    padding-bottom: 20px;
  }
  &__nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 70%;

    &__item {
      @include mobile {
        margin-right: 40px;
        margin-bottom: 20px;
      }
    }

    &-btn {
      font-weight: 400;
      font-size: 20px;
      line-height: 32px;
      color: var(--dark-color);

      &:focus {
        background-color: transparent;
        color: var(--accent-color);
      }

      &--active {
        outline: none;
        background-color: transparent;
        color: var(--accent-color);
      }
    }

    @include mobile {
      flex-wrap: wrap;
    }
  }
}
