.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background-color: #d52027;
  border-radius: 5px;
  padding: 26px 54px;
  font-weight: 400;
  font-size: 18px;
  line-height: 29px;
  text-align: center;
  color: var(--light-color);

  &--dark {
    background: var(--dark-color);
  }
}
