.trainers {
  &__wrapper {
    padding-left: max(15px, calc((100% - var(--content-width)) / 2));
  }

  &__content {
    margin-bottom: 75px;
  }

  &__image {
    border-radius: 20px;
    margin-bottom: 20px;
    max-height: 500px;
  }

  &__name {
    margin: 0;
    margin-bottom: 10px;
    font-weight: 600;
    font-size: 24px;
    line-height: 38px;
    color: var(--dark-color);
  }

  &__post {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 400;
    font-size: 18px;
    line-height: 29px;
    color: var(--dark-color07);
  }

  &__btn {
    font-weight: 400;
    font-size: 18px;
    line-height: 29px;
    color: var(--accent-color);
  }

  &__nav {
    @include flex-all-sb;
  }
  &__scroll {
    border-radius: 40px;
    width: 360px;
    height: 5px;
    background: rgba(20, 16, 36, 0.08);
  }
  &__btns {
    @include flex-v-center;
    gap: 10px;

    @include mobile {
      display: none;
    }
  }

  &__slider-btn {
    @include bg-cover;

    width: 40px;
    height: 40px;

    &--prev {
      background-image: url("../img/prev.svg");
    }
    &--next {
      background-image: url("../img/next.svg");
    }
  }
}
.swiper-scrollbar-drag {
  background-color: var(--dark-color);
}

.swiper-slide {
  width: 360px;

  @include mobile {
    width: 220px;
  }
}
